import ROUTE_MAP from '../config/routeMap';
//todo 需添加一些容错处理

/**
 *
 * @param path
 * @return {{path: string, component: *, children: *[]}[]}
 */
const getRouteByPath = (path) => {
  const pathArray = path.split('/').filter(item => !!item);
  if (pathArray.length === 0) {
    return ROUTE_MAP;
  }
  let deep = 0;
  const targetDeep = pathArray.length;
  const loopList = [ROUTE_MAP];

  while (loopList.length > 0) {
    const routes = loopList.pop();
    const route = routes.find(item => item.path === pathArray[deep]);
    if (!route) return;
    deep += 1;
    if (deep === targetDeep) {
      return route;
    }
    const {children} = route;
    if (!children) return;
    loopList.push(children);
  }
};

const getSubRoutesByPath = (path) => {
  if (path === '/') {
    return ROUTE_MAP;
  }
  return getRouteByPath(path).children;
};

export {
  getRouteByPath,
  getSubRoutesByPath,
};