import states from './states';
import {SET_IS_CHINESE} from './actionTypes';

const reducer = (state = states, action) => {
  const {type, payload} = action;
  switch (type) {
    case SET_IS_CHINESE:
      return {
        ...state,
        isChinese: payload,
      }
    default:
      return state;
  }
};

export default reducer;